.catalog {
  padding: 85px 0 120px;

  @include vp-1023 {
    padding: 90px 0 100px;
  }

  @include vp-767 {
    padding: 57px 0 65px;
  }
}

.catalog__inner {
  display: flex;
  flex-direction: column;
  justify-content: center;

  h2 {
    margin: 0;
    margin-bottom: 49px;
    text-align: center;
    font-weight: 600;
    font-size: 28px;
    line-height: 42px;

    @include vp-1023 {
      font-size: 26px;
      line-height: 39px;
    }

    @include vp-767 {
      margin-bottom: 30px;
      font-size: 24px;
      line-height: 36px;
    }
  }
}

.catalog__card-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(auto, 23%));
  grid-auto-rows: 1fr;
  grid-auto-flow: dense;
  grid-gap: 30px;
  margin: 0 15px;
  justify-content: center;

  @include vp-1023 {
    grid-template-columns: repeat(auto-fit, minmax(auto, 32%));
  }

  @include vp-767 {
    grid-template-columns: repeat(auto-fit, minmax(270px, 360px));
    margin: 0 10px;
  }
}

.catalog__card {
  position: relative;
  color: $color-alice-blue;
  width: 100%;
  height: 100%;
  min-width: 260px;
  max-height: 396px;

  @include vp-1023 {
    min-width: 309px;
  }

  @include vp-767 {
    min-width: 270px;
  }

  @include hover-focus {
    outline: none;

    h3 {
      opacity: 0;
    }

    .catalog__card-inner {
      opacity: 1;
      background-color: $color-tangaroa;

      h3 {
        opacity: 1;
      }
    }
  }

  &:focus-within {
    h3 {
      opacity: 0;
    }

    .catalog__card-inner {
      opacity: 1;
      background-color: $color-tangaroa;

      h3 {
        opacity: 1;
      }
    }
  }

  img {
    position: absolute;
    z-index: -1;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: $color-nepal;
    object-fit: cover;
    width: 100%;
    height: 100%;

    @include vp-767 {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }

  h3 {
    position: absolute;
    bottom: 0;
    left: 0;
    margin: 0 30px 22px;
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    transition: $trans-default opacity;

    @include vp-1023 {
      margin: 0 35px 27px;
    }

    @include vp-767 {
      margin: 0 24px 20px;
    }
  }
}

.catalog__card-inner {
  display: flex;
  flex-direction: column;
  overflow: auto;
  opacity: 0;
  padding: 25px 25px 35px;
  transition: $trans-600 background-color, opacity 0.4s;
  width: 100%;
  height: 100%;

  @include vp-1023 {
    padding: 25px 35px 35px;
  }

  @include vp-767 {
    padding: 25px 25px 35px;
  }

  h3 {
    position: static;
    margin: 0;
    margin-bottom: 11px;
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;

    @include vp-1023 {
      margin: 0;
      margin-bottom: 11px;
    }

    @include vp-767 {
      margin: 0;
      margin-bottom: 11px;
    }
  }

  span {
    display: block;
    font-weight: 500;
    font-size: 14px;
  }

  p {
    display: block;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    margin: 0;
    margin-bottom: 8px;
  }
}

.catalog__btn {
  height: 40px;
  margin-top: auto;
}
